.ant4-colorpicker {
  position: absolute;
  top: 40px;
  z-index: 999;
  right: -10px;
  width: 280px;
  padding: 10px;
  border-radius: 2px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 0px 1px, rgba(0, 0, 0, 0.15) 0px 8px 16px;
}
.ant4-colorpicker-display {
  transition: all 0.5s;
  position: relative;
  background-color: transparent;
}
.ant4-colorpicker-display > span {
  display: block;
  width: 25px;
  height: 25px;
  border-radius: 2px;
  background-color: white;
  transition: all 0.5s;
}
.ant4-colorpicker-full .ant4-input {
  position: absolute;
  left: -100%;
  top: -100%;
  visibility: hidden;
}
.ant4-colorpicker-full .ant4-input-suffix {
  flex: 1;
  margin-left: 0;
}
.ant4-colorpicker-full.ant4-input-affix-wrapper-focused,
.ant4-colorpicker-full.ant4-input-affix-wrapper:focus {
  z-index: auto !important;
}
.ant4-colorpicker-full .ant4-colorpicker-display {
  width: 100%;
}
.ant4-colorpicker-full .ant4-colorpicker-display > span {
  width: 100%;
}
.ant4-colorpicker .sketch-picker {
  width: 100% !important;
  box-sizing: border-box !important;
  box-shadow: none !important;
  padding: 0 !important;
}
.ant4-colorpicker-radio {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 10px;
}
.ant4-colorpicker-angle {
  height: 32px;
}
.ant4-colorpicker-angle .ap,
.ant4-colorpicker-angle > * {
  width: 32px !important;
  height: 32px !important;
}
.ant4-colorpicker-angle .apc {
  height: 32px !important;
}
.ant4-colorpicker-angle .ant4-input-number {
  width: 100%;
}
