.split-color-wrapper-style {
  text-align: center;
}
.split-color-wrapper-style .split-color-tip {
  padding: 20px 0 10px 0;
}
.split-color-wrapper-style .step-style {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.split-color-wrapper-style .step-style:last-child {
  margin-bottom: 0;
}
.split-color-wrapper-style .step-style > div:nth-child(1) {
  flex: 1;
}
.split-color-wrapper-style .anticon::before {
  display: none !important;
}
