.component-data-setting .anticon::before,
.ant4-modal-root .anticon::before {
  display: none !important;
}
.component-data-setting .anticon svg,
.ant4-modal-root .anticon svg {
  display: inline-block !important;
}
.anticon svg {
  display: none !important;
}
.component-settings .tab-pane .component-settings-wrapper .anticon::before {
  display: none !important;
}
.component-settings .tab-pane .component-settings-wrapper .anticon svg {
  display: inline-block !important;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-series-wrapper {
  background-color: #fafafa;
  padding-top: 12px;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-series-wrapper > div:nth-child(1),
.component-settings .tab-pane .component-settings-wrapper .ant4-series-wrapper > div:nth-child(2) {
  margin-left: 16px;
  margin-right: 16px;
  max-width: calc(100% - 32px);
}
.component-settings .tab-pane .component-settings-wrapper .ant4-gap {
  margin-bottom: 12px;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-padding-wrapper {
  min-height: 35px;
  margin: 12px 16px 0 16px;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-input-number {
  width: 100%;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-form {
  position: relative;
  margin-bottom: 24px;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-form::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #d9d9d9;
  position: absolute;
  bottom: -12px;
  left: 0;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-form:last-child {
  margin-bottom: 0;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-form:last-child::after {
  display: none;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-form-item:last-child {
  margin-bottom: 0;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-form-item-label {
  text-align: left;
}
.component-settings .tab-pane .component-settings-wrapper .ant4-collapse > .ant4-custom-header {
  padding: 12px 16px;
}
